"use client";

import { useState, useEffect } from "react";
import {
  Search,
  Calendar as CalendarIcon,
  Users,
  Filter,
  ChevronDown,
  ChevronUp,
  Plus,
  Mic,
  RefreshCw,
  X,
} from "lucide-react";
import { SiGooglemeet } from "react-icons/si";
import { PiMicrosoftTeamsLogoFill } from "react-icons/pi";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Label } from "@/components/ui/label";
import { isWithinInterval, subDays } from "date-fns";
import Link from "next/link";
import { DateRangePicker } from "@/components/ui/date-range-picker";
import { DateRange } from "react-day-picker";
import { MeetingList, Meeting } from "../components/meeting-list";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { createClient } from "@/utils/supabase/client";
import { NewMeetingOptions } from "../components/new-meeting-options";

export default function MeetingsPage() {
  const [searchQuery, setSearchQuery] = useState("");
  const [dateRange, setDateRange] = useState<DateRange | undefined>({
    from: subDays(new Date(), 30),
    to: new Date(),
  });
  const [meetingTypeFilter, setMeetingTypeFilter] = useState<string>("all");
  const [showFilters, setShowFilters] = useState(false);
  const [meetings, setMeetings] = useState<Meeting[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isRefreshing, setIsRefreshing] = useState(false);

  const fetchMeetings = async () => {
    try {
      const supabase = createClient();
      const {
        data: { user },
      } = await supabase.auth.getUser();

      if (!user) {
        throw new Error("User not authenticated");
      }

      const { data, error } = await supabase
        .from("meetings")
        .select("*")
        .eq("user_id", user.id)
        .order("created_at", { ascending: false });

      if (error) {
        throw error;
      }

      // Transform the data to match the Meeting type
      const transformedMeetings: Meeting[] = (data || []).map(meeting => {
        const startTime = meeting.start_time ? new Date(meeting.start_time) : new Date(meeting.created_at);
        const endTime = meeting.end_time ? new Date(meeting.end_time) : new Date(startTime.getTime() + 3600000);
        
        return {
          id: meeting.id,
          title: meeting.title,
          startTime,
          endTime,
          meeting_type: meeting.meeting_type,
          transcription_status: meeting.transcription_status || 'pending',
          summary_status: meeting.summary_status || 'pending',
          participants: meeting.participants || [],
        };
      });

      setMeetings(transformedMeetings);
    } catch (error) {
      console.error("Error fetching meetings:", error);
    } finally {
      setIsLoading(false);
      setIsRefreshing(false);
    }
  };

  useEffect(() => {
    fetchMeetings();
  }, []);

  const handleRefresh = async () => {
    setIsRefreshing(true);
    await fetchMeetings();
  };

  const filteredMeetings = meetings.filter((meeting) => {
    const matchesSearch =
      meeting.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      meeting.participants.some((p) =>
        p.name.toLowerCase().includes(searchQuery.toLowerCase())
      );

    const matchesDate =
      !dateRange?.from ||
      !dateRange?.to ||
      isWithinInterval(meeting.startTime, {
        start: dateRange.from,
        end: dateRange.to,
      });

    const matchesType =
      meetingTypeFilter === "all" || meeting.meeting_type === meetingTypeFilter;

    return matchesSearch && matchesDate && matchesType;
  });

  const clearFilters = () => {
    setSearchQuery("");
    setDateRange({
      from: subDays(new Date(), 30),
      to: new Date(),
    });
    setMeetingTypeFilter("all");
  };

  const hasActiveFilters =
    searchQuery !== "" ||
    meetingTypeFilter !== "all" ||
    (dateRange?.from && dateRange.from !== subDays(new Date(), 30)) ||
    (dateRange?.to && dateRange.to !== new Date());

  return (
    <div className="container mx-auto p-6 max-w-7xl space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-2xl font-semibold">Møter</h1>
          <p className="text-muted-foreground">
            Administrer og få oversikt over alle møtene dine
          </p>
        </div>
        <div className="flex items-center gap-2 w-full sm:w-auto">
          <Button
            variant="outline"
            size="icon"
            onClick={handleRefresh}
            disabled={isRefreshing}
            className={isRefreshing ? "animate-spin" : ""}
          >
            <RefreshCw className="h-4 w-4" />
          </Button>
          <Button
            className="w-full sm:w-auto bg-violet-600 hover:bg-violet-700"
            onClick={() => {
              const element = document.getElementById('new-meeting-options');
              if (element) {
                element.scrollIntoView({ behavior: 'smooth' });
              }
            }}
          >
            <Plus className="h-4 w-4 mr-2" />
            Nytt møte
          </Button>
        </div>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="p-4 sm:p-6">
          <div className="space-y-4">
            <div className="flex flex-col sm:flex-row gap-4">
              <div className="flex-1">
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                  <Input
                    placeholder="Søk i møter..."
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    className="pl-9"
                  />
                </div>
              </div>
              <div className="flex items-center gap-2">
                <Button
                  variant="outline"
                  className="w-full sm:w-auto"
                  onClick={() => setShowFilters(!showFilters)}
                >
                  <Filter className="h-4 w-4 mr-2" />
                  Filter
                  {showFilters ? (
                    <ChevronUp className="h-4 w-4 ml-2" />
                  ) : (
                    <ChevronDown className="h-4 w-4 ml-2" />
                  )}
                </Button>
                {hasActiveFilters && (
                  <Button
                    variant="ghost"
                    size="icon"
                    onClick={clearFilters}
                    className="hover:bg-destructive/10 hover:text-destructive"
                  >
                    <X className="h-4 w-4" />
                  </Button>
                )}
              </div>
            </div>

            {showFilters && (
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 pt-4 border-t">
                <div className="space-y-2">
                  <Label>Dato</Label>
                  <DateRangePicker date={dateRange} onSelect={setDateRange} />
                </div>
                <div className="space-y-2">
                  <Label>Møtetype</Label>
                  <Select
                    value={meetingTypeFilter}
                    onValueChange={setMeetingTypeFilter}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Velg møtetype" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="all">Alle typer</SelectItem>
                      <SelectItem value="microsoft-teams">Microsoft Teams</SelectItem>
                      <SelectItem value="google-meets">Google Meet</SelectItem>
                      <SelectItem value="live">Live opptak</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Results */}
      <div className="grid grid-cols-1 xl:grid-cols-[1fr,350px] gap-6">
        <div className="space-y-4">
          {hasActiveFilters && (
            <div className="flex items-center gap-2 text-sm text-muted-foreground">
              <span>Aktive filtre:</span>
              {searchQuery && (
                <Badge variant="secondary" className="font-normal">
                  Søk: {searchQuery}
                </Badge>
              )}
              {meetingTypeFilter !== "all" && (
                <Badge variant="secondary" className="font-normal">
                  Type: {meetingTypeFilter}
                </Badge>
              )}
              {dateRange && (
                <Badge variant="secondary" className="font-normal">
                  Dato: {dateRange.from?.toLocaleDateString()} - {dateRange.to?.toLocaleDateString()}
                </Badge>
              )}
            </div>
          )}

          {isLoading ? (
            <Card>
              <CardContent className="p-8">
                <div className="flex justify-center">
                  <div className="animate-spin">
                    <RefreshCw className="h-5 w-5 text-muted-foreground" />
                  </div>
                </div>
              </CardContent>
            </Card>
          ) : (
            <MeetingList meetings={filteredMeetings} />
          )}
        </div>

        {/* Right Sidebar */}
        <div className="space-y-6">
          <div id="new-meeting-options">
            <NewMeetingOptions />
          </div>
          
          {/* Stats Card */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Statistikk</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-1">
                  <p className="text-2xl font-semibold">{meetings.length}</p>
                  <p className="text-sm text-muted-foreground">Totalt antall møter</p>
                </div>
                <div className="space-y-1">
                  <p className="text-2xl font-semibold">
                    {meetings.filter(m => m.transcription_status === 'completed').length}
                  </p>
                  <p className="text-sm text-muted-foreground">Transkriberte møter</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
